import React, {useEffect} from 'react';
import { GEOMS, makeShape, RAD } from '../threeDimension/Geometries';
import * as THREE from 'three';
import ThreeArea, { playSection } from '../threeDimension/ThreeArea';
import drawAnnotation from '../threeDimension/drawAnnotation';

const fullSize = {width:'100vw', height:'100vh'};

const loader = new THREE.TextureLoader();

let lines = [];

const arworksSetup = info => ({scene, camera, root}) => {  
  const planeGeometry = new THREE.PlaneGeometry(500,500, 5);
  const marker = new THREE.MeshBasicMaterial({
    map: loader.load('/sources/desk-marker-rect.jpg'),
    side: THREE.DoubleSide
  })
  const plane = new THREE.Mesh(planeGeometry, marker);
  const item = makeShape(GEOMS.box, 50, 200, 20, 0x888888, 1 , 0x000000);
  lines =  [
    [0x8888FF, [0,-100,0], [0,-200,0], info.v10, 'v10'],
    [0x8888FF, [0,100,0], [0,200,0], info.v10, 'v10'],

    [0xFFDD00, [0,50,10], [0,100,100], info.v00, 'v00'],

    [0xDDFF00, [0,-50,10], [0,-100,100], info.v20, 'v20'],

    [0x44FF44, [0,0,10], [-20,-20,120], info.v21, 'v21'],

    [0xFF00FF, [10,70,10], [50,50,100], info.v30, 'v30'],

    [0xFF5577, [-10,-70,10], [-50,-150,100], info.v31, 'v31'],
  ].map(data => drawAnnotation(...data));
  
  plane.rotateX(-70 / RAD).rotateY(1 / RAD).rotateZ(1 / RAD);
  item.position.setZ(10.1);
  plane.add(item);
  lines.map(({line}) => item.add(line));
  plane.position.setY(-50);
  scene.add(plane);

  return playSection(() => {
    plane.rotateZ(.21/RAD)
  }, {scene});    
}

export default ({annotation}) => {

  useEffect(() => {
    lines.map(({line, key}) => {
      const info = annotation[key]      
      line.visible = info && info.checked && annotation[info.link] && annotation[info.link].checked;  
    })
  }, [annotation]);

  return <ThreeArea add3DContent={arworksSetup(annotation)} style={fullSize} isOrbit={true} />;
}

