import React from 'react';
import MainPage from './MainPage';
import InfoPage from './InfoPage';
import QrPage from './QrPage';
import Positioning from './Positioning';
import ArworksPage from './ArworksPage';
import { routes } from './../hooks/useDemoFrame';

export default ({path, navigation}) => {
  
  switch (path) {
    case routes.MAIN     : return <MainPage navigation={navigation} />;
    case routes.INFO     : return <InfoPage navigation={navigation} />;
    case routes.QR_READ  : return <QrPage navigation={navigation} />;
    case routes.POSITION : return <Positioning navigation={navigation} />;
    case routes.REALITY  : return <ArworksPage navigation={navigation} />;
    default              : return <div className='page'>- - undefined page - -</div>;
  }
}