import React, { useState } from 'react';
import { divFactory, noprop } from "react-slash";
import QrReadingReality from './QrReadingReality';

const [Page, Welcome, NextButton, CancelButton, ButtonLine] = divFactory('page', 'welcome', 'modal-next-button eventsAuto', 'modal-cancel-button eventsAuto', 'button-line');
const [ModalWindow, ModalHeader, ModalContent] = divFactory('modal-window', 'modal-header', 'modal-content');
const [Layer] = divFactory('page page-layer transparent eventsNone');

export default ({navigation}) => {
  const [modelName, setModelName] = useState("Teszt Model");
  const [isModal, showModal] = useState(false);

  const clickCancel = () => showModal(false);
  const clickToPosition = () => navigation('position');

  const InfoModal = () => ( 
    <ModalWindow>
      <ModalHeader>A tárgy amit beolvastál</ModalHeader>
      <ModalContent>
        <p>{modelName}</p>
      </ModalContent>
      <ButtonLine>
        <CancelButton onClick={noprop(clickCancel)}>Mégse</CancelButton>
        <NextButton onClick={noprop(clickToPosition)}>Tovább</NextButton>
      </ButtonLine>
    </ModalWindow>
  )

  return (
    <Page>
      <Layer>
        <QrReadingReality clickHandler={name=>{setModelName(name);showModal(true)}} />
      </Layer>
      { isModal && <InfoModal />}
    </Page>
  );
}
