import * as THREE from 'three';

export const hexToRgba = (color, a=1) => ({
  r: (color & 0xFF0000) >> 16,
  g: (color & 0x00FF00) >> 8,
  b: color & 0xFF > 8,
  a
});

export const hexToColor = color => `#${color.toString(16)}`;

// http://jsfiddle.net/asyukri/ajhw0jsz/
export default (message, fontColor, materialColor) => {
  var fontface = "sans-serif";
  var fontsize = 48;
  var borderThickness = 4;
  var canvas = document.createElement('canvas');
  var context = canvas.getContext('2d');
  context.font = `${fontsize}px ${fontface}`;

  var metrics = context.measureText(message);
  // context.fillStyle = `rgba(${fontColor.r},${fontColor.g},${fontColor.b},${fontColor.a})`;
  context.fillStyle = fontColor;
  context.fillText(message, borderThickness, fontsize + borderThickness);

  var texture = new THREE.Texture(canvas)
  texture.needsUpdate = true;

  var spriteMaterial = new THREE.SpriteMaterial({
    map: texture,
    color: materialColor || 0xffffff
  });
  var sprite = new THREE.Sprite(spriteMaterial);
  sprite.scale.set(100, 50, 1.0);
  return sprite;
}


