import React, {useEffect} from 'react';
import { divFactory, showFactory } from "react-slash";
import Route from "./Route";
import { pathsNumber, quickInfo } from '../data/demo';
import useDemoFrame from '../hooks/useDemoFrame';

import '../styles/index.scss';
import '../styles/demo-frame.scss';
import '../styles/ar-page.scss';

const [DemoLine, DemoIndicator] = divFactory('demo-line', 'demo-indicator');
const [DemoApplicationFrame] = divFactory('demo-app-frame')
const [QuickInfoPanel, QuickNextButton] = showFactory('quick-info-panel', 'quick-next');

export default () => {
  const {state, navigation, hideQuick, showQuick} = useDemoFrame();
  const {path, quick: {detail, top, left}, isQuick} = state;

  useEffect(() => {
    showQuick(quickInfo.setupAnnotation);
  }, []);
  

  return (
    <>
      <DemoApplicationFrame>
        <Route path={path} navigation={navigation} />
      </DemoApplicationFrame>
      <DemoLine>
        {Object.values(pathsNumber).map(({num, name}, i) => 
          <DemoIndicator data-selected={name === path} key={i} onClick={() => navigation(name)}>{num}</DemoIndicator>
        )}
        <span>{pathsNumber[path].title}</span>
      </DemoLine>
      <QuickInfoPanel show={isQuick} style={{top:`${top}%`, left:`${left}%`}}>
        {detail}
        <QuickNextButton show={true} onClick={() => hideQuick()}>következő</QuickNextButton>
      </QuickInfoPanel>
    </>
  );
}

