import React, {useEffect, useState} from 'react';
import { GEOMS, makeShape, RAD } from '../threeDimension/Geometries';
import * as THREE from 'three';
import ThreeArea, { playSection } from '../threeDimension/ThreeArea';
import { pipe, forEach, take, map } from 'callbag-basics';
import fromEvent from 'callbag-from-event';
import { clickSource, filter, middleLog } from '../utils/callbagUtills';

const fullSize = {width:'100vw', height:'100vh'};

const loader = new THREE.TextureLoader();

const qrReadingSetup = qrCodeReaded => ({scene, camera, root}) => {  
  const planeGeometry = new THREE.PlaneGeometry(50,50, 5);
  const marker = new THREE.MeshBasicMaterial({    
    map: loader.load('/sources/teszt-test-qr-256.png'),
    side: THREE.DoubleSide,    
  })

  const qrCode = new THREE.Mesh(planeGeometry, marker);
  const plane = makeShape(GEOMS.box, 500, 500, 10, 0x555555, 1 , 0x000000);

  const items = [
    {item: makeShape(GEOMS.box, 50, 200, 20, 0x88DDDD, 1 , 0x000000), move: {z:10.1, y:-50, x:-200}},
    {item: makeShape(GEOMS.box, 50, 200, 20, 0x888888, 1 , 0x000000), move: {z:10.1, y:-50, x:-100}},
    {item: makeShape(GEOMS.box, 50, 200, 20, 0x88DD88, 1 , 0x000000), move: {z:10.1, y:-50, x:0}},
    {item: makeShape(GEOMS.box, 50, 200, 20, 0x8888DD, 1 , 0x000000), move: {z:10.1, y:-50, x:100}},
    {item: makeShape(GEOMS.box, 50, 200, 20, 0xDD8888, 1 , 0x000000), move: {z:10.1, y:-50, x:200}},
  ]
  
  plane.rotateX(-70 / RAD).rotateY(1 / RAD).rotateZ(1 / RAD);

  items.map(({item, move:{x,y,z}}, index) => {
    item.position.set(x, y, z);
    const qr = qrCode.clone();
    qr.position.setY(-150).setZ(-4.9);
    item.add(qr);  
    item.name = `${index+1}. munkadarab`;
    plane.add(item);
  });
  
  scene.add(plane);

  pipe(
    clickSource(camera, plane.children),    
    map(mesh => mesh.name),
    // take(1),
    forEach(qrCodeReaded)
  )

  return playSection(() => {
    // plane.rotateZ(.121/RAD)
  }, {scene});    
}

export default ({clickHandler}) => {
  return (
    <ThreeArea 
      add3DContent={qrReadingSetup(clickHandler)} 
      style={fullSize} 
      isOrbit={true} 
    />
  );
}

