import React, { useState } from 'react';
import { divFactory, noprop } from 'react-slash';

const [NextButton, CancelButton, ButtonLine] = divFactory('modal-next-button eventsAuto', 'modal-cancel-button eventsAuto', 'button-line');
const [ModalWindow, ModalHeader] = divFactory('modal-window --positioning-modal', 'modal-header');
const [FixingButton, CenterLayer] = divFactory('fixing-button eventsAuto', 'page center-layer transparent');

const [Layer, Page] = divFactory('page page-layer transparent eventsNone', 'page');
import PositioningReality from './PositioningReality';

const transparent = {background: 'transparent', pointerEvents: 'none'};
const catchEvent = {background: 'transparent', pointerEvents: 'auto'};

export default ({navigation, modelName="Teszt Model"}) => {
  const [isModal, showModal] = useState(false);
  const clickCancel = () => showModal(false);
  const clickToReality = () => navigation('reality');

  const InfoModal = () => ( 
    <ModalWindow>
      <ModalHeader>Sikerült pozícionálni a tárgyat?</ModalHeader>
      <ButtonLine>
        <CancelButton onClick={noprop(clickCancel)}>Újra</CancelButton>
        <NextButton onClick={noprop(clickToReality)}>Igen</NextButton>
      </ButtonLine>
    </ModalWindow>
  )

  return (
    <Page>
      <Layer>
        <PositioningReality annotation={{}} />
      </Layer>    
      <Layer>
        { isModal 
          ? <CenterLayer><InfoModal /></CenterLayer>
          : <FixingButton onClick={()=>showModal(true)}>Rögzítés</FixingButton>
        }
      </Layer>
    </Page>
  );
}
