import { useReducer } from 'react';
import { actionCreator } from 'react-slash';

export const initialAnnotations = {
  m0: {title: 'Tömeg', checked: true },
    v00: {measure: '10 kg', date:'(1/4/2019)', checked: true, link:'m0' },
  m1: {title: 'Távolság két pont között', checked: true },
    v10: {measure: '55 mm', date:'(1/3/2019)', checked: true, link:'m1' },
  m2: {title: 'Hardness, HV', checked: true },
    v20: {measure: '340 Rockwell 501', date:'(5/12/2019)', checked: false, link:'m2' },
    v21: {measure: '342 Rockwell 501', date:'(5/13/2019)', checked: true, link:'m2' },
  m3: {title: 'Tensile strength', checked: true },
    v30: {measure: '505 Instron', date:'(5/12/2019)', checked: true, link:'m3' },
    v31: {measure:'520 Instron', date:'(5/12/2019)', checked: true, link:'m3' },
};

const [CHANGE, changeAction] = actionCreator('change');
const measureReducer = (state, {type, payload}) => {
  switch (type) {
    case CHANGE : 
      if (!state[payload]) return state;      
      const measure = state[payload];
      const checked = !measure.checked;
      return {...state, [payload]: {...measure, checked} };
    default: return state;
  }
};

export default () => {
  const [state, dispatch] = useReducer(measureReducer, initialAnnotations);
  const onChange = checkBoxKey => () => dispatch(changeAction(checkBoxKey));
  return {state, dispatch, onChange};
}