import React from 'react';
import { divFactory, stopProp, showtime } from "react-slash";

const [ModalWindow, Title ] = divFactory('ar-modal-window', 'ar-modal-title');
const [ArCheckBox] = divFactory('ar-check-box');

const CheckBox = ({checked, ...props}) => <ArCheckBox data-checked={checked} {...props} />
const ShowBox = showtime(CheckBox);

const tab = {marginLeft:'2em', height:'4em'};

const MeasuresSelectors = ({annotations, change}) => Object
  .entries(annotations)
  .map(([key, {checked, title, measure, date, link}]) => 
    ( title 
      ? <CheckBox key={key} data-checked={checked} onClick={change(key)}>{title}</CheckBox>
      : <ShowBox key={key} data-checked={checked} show={annotations[link] && annotations[link].checked} style={tab} onClick={change(key)}>{measure} <span>{date}</span></ShowBox>
    ));

export default ({state, dispatch, onChange}) => {
  return (
    <ModalWindow onClick={stopProp}>
      <Title>Annotation</Title><hr/>   
      <MeasuresSelectors annotations={state} dispatch={dispatch} change={onChange} />
    </ModalWindow>
  );
}