import * as THREE from 'three';
import drawLine from './drawLine';
import makeTextSprite, {hexToColor} from './makeTextSprite';
import { makeShape, GEOMS, RAD } from './Geometries';

export default (color = 0xFF00FF, a = [0,0,0], b = [0,0,100], info = {}, key) => {
  const {measure} = info;
  const line = drawLine(color, a, b);
  const aPos = new THREE.Vector3(...a);
  const bPos = new THREE.Vector3(...b);

  const arrow = makeShape(GEOMS.cone, 5, 20, 5, color);
  arrow.geometry.applyMatrix(new THREE.Matrix4().makeTranslation(0,-10,0)); // move origo to top;
  arrow.position.add(aPos);  
  arrow.lookAt(bPos);
  arrow.rotateX(-90/RAD);  
  line.add(arrow);

  if (measure) {
    const text = makeTextSprite(measure, hexToColor(color));
    text.position.add(bPos);
    line.add(text);
  }  

  return {
    line,
    key
  };
}
