import React, {useEffect, useState} from 'react';
import { GEOMS, makeShape, RAD } from '../threeDimension/Geometries';
import * as THREE from 'three';
import ThreeArea, { playSection } from '../threeDimension/ThreeArea';
import { makeWireframe } from './../threeDimension/Geometries';
import { pipe, forEach, take, map } from 'callbag-basics';
import fromEvent from 'callbag-from-event';
import { clickSource, filter, middleLog, distancedMove } from '../utils/callbagUtills';

const fullSize = {width:'100vw', height:'100vh'};

const loader = new THREE.TextureLoader();

const positioningSetup = ({scene, camera, root}) => {  
  const planeGeometry = new THREE.PlaneGeometry(500,500, 5);
  const marker = new THREE.MeshBasicMaterial({
    map: loader.load('/sources/desk-marker-rect.jpg'),
    side: THREE.DoubleSide
  })
  const plane = new THREE.Mesh(planeGeometry, marker);
  const item = makeShape(GEOMS.box, 50, 200, 20, 0x888888, 1 , 0x000000);
  const target = makeWireframe(GEOMS.box, 50, 200, 20, 0x0000FFF);
  
  plane.rotateX(-70 / RAD).rotateY(1 / RAD).rotateZ(1 / RAD);
  target.position.setZ(10.1);
  item.position.setZ(110.1);
  plane.add(item);
  plane.add(target);
  plane.position.setY(-50);
  scene.add(plane);

  pipe(
    clickSource(camera, [item]),    
    take(1),
    forEach(() => {
      const {z} = item.position;
      distancedMove( zc => item.position.z = z - zc);
    })
  );
  

  return playSection(() => {
    plane.rotateZ(-.21/RAD)
  }, {scene});    
}

export default ({annotation}) => {
  return <ThreeArea add3DContent={positioningSetup} style={fullSize} isOrbit={true} />;
}

